.todo-item {
    background-color: aqua;
    padding: 1rem;
    margin: 0.5rem;
    /* border: 1px solid black; */
    box-shadow: 1px 1px 2px;
    transition: all 0.5s linear;
    user-select: none;
    animation-fill-mode: both;
}

.todo-item:active {
    background-color: red;
    margin-right: 2rem;
    margin-left: 2rem;
    opacity: 0%;
}

.todo-item__text {
    font-size: 22px;
}

